import Img2Base64 from "@/common/img2Base64.js"
export default {
	data() {
		return {
			img: Img2Base64,
			SessionKey: '',
			OpenId: '',
			nickName: null,
			avatarUrl: null,
			isCanUse: !uni.getStorageSync('userinfo') //默认为true
		};
	},
	methods: {
		//第一授权获取用户信息===》按钮触发
		wxGetUserInfo() {
			let _this = this;
			// 1.wx获取登录用户code
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					let code = loginRes.code;
					console.info(code)
					//2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
					uni.request({
						url: 'https://www.doaho.work:8080/user/login',
						data: {
							code: code,
						},
						method: 'GET',
						header: {
							'content-type': 'application/json'
						},
						success: (res) => {
							//openId、或SessionKdy存储//隐藏loading
							console.info(res)
							if (res.data.success) {
								_this.$store.commit("login", res.data.data)
								uni.getUserInfo({
									provider: 'weixin',
									success: function(infoRes) {
										//获取用户信息后向调用信息更新方法
								
										console.info('登陆中')
										uni.showLoading({
											title: '登录中...'
										});
										console.info(infoRes)
										_this.$store.commit('updateUserInfo', infoRes.userInfo)
										// let nickName = infoRes.userInfo.nickName; //昵称
										// let avatarUrl = infoRes.userInfo.avatarUrl; //头像
										// _this.updateUserInfo(); //调用更新信息方法
										
										uni.hideLoading()
										uni.navigateBack()
									},
									fail() {
										_this.$store.commit('logout')
										uni.showToast({
											title:"您取消了授权",
											icon:"none"
										})
									}
								});
							}
						}
					});
				}
			});
		},
	}
}
